/*********************************
11.1 Search Form
*********************************/

.search_form
{
	margin-top: 57px;
}
.input_field
{
	width: 100%;
	height: 42px;
	background: #FFFFFF;
	box-sizing: border-box;
	border: solid 2px #FFFFFF;
	padding-left: 25px;
	margin-bottom: 24px;
}
/* input:last-of-type
{
	margin-bottom: 0px;
} */
.input_field:focus
{
	outline: none !important;
	border: solid 2px #ffb606;
}
